  getSessionStackSession(): Promise<string> {
    return new Promise(resolve => {
      if (window['SessionStack']) {
        window['SessionStack'].getSessionId(sessionId => {
          this.log.log('SessionStack sessionid', sessionId);
          resolve(sessionId);
        });
      } else {
        resolve(null);
      }
    });
  }

